body {
    font-family: monospace;
    margin: 0;
    padding: 0;
    background-color: #212121;
}

.container {
    max-width: 650px;
    margin: 0px  auto;
    text-align: center;
}

h1 {
    margin-bottom: 20px;
    color: #2f2f2f;
}

input[type="text"] {
    width: 100%;
    padding: 14px;
    font-size: 16px;
    border: 0px solid #ccc;
    margin-bottom: 20px;
    border-radius: 1000px;
    background-color: #2f2f2f;
}

ul {
    list-style-type: none;
    padding: 0;
    min-height: 1000px;
}

li {
    margin: 10px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #00000028;  
    padding: 5px;
    color: #9b9b9b; 
    /* border-radius: 5px; */
    box-shadow: 0 2px 4px  #00000028;  

}
li:hover {
    color: #45a049; 
}
code {
    /* background-color: #eee; */
    padding: 2px 4px;
    /* border-radius: 4px; */
    font-family: monospace;
    font-size: 14px;
    align-items: flex-start;
}
 
li:last-child {
    border-bottom: none;
}
.copy-button {
    background-color: #2b2a2aba; 
    border: none; 
    color: #9b9b9b; 
    padding: 5px 8px; 
    text-align: center; 
    text-decoration: none; 
    display: inline-block; 
    font-size: 12px; 
    margin-left: 2px; 
    cursor: pointer; 
    border-radius: 2px; 
    transition: background-color 0.3s; 

}
.copy-button:hover {
    background-color: #45a049; 
    color: white;
}
.copy-button i {
    margin-right: 0px;
    /* color: #000; */
} 
.copy-button i:hover {
    margin-right: 0px;
   
} 